import React from 'react';
import styled from 'styled-components';
import shopData from '../../data/NEW/Shop';

export default class BookingTime extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            shopData: shopData,
            working: true,
            timeArray: [],
            partyName: '',
            partyType: '',
            partySize: 0,
            maxParty: 99,
        }

        this.submit = this.submit.bind(this);
        this.handlePartyType = this.handlePartyType.bind(this);
        this.handlePartyName = this.handlePartyName.bind(this);
        this.createBranchTime = this.createBranchTime.bind(this);
    }

    componentDidMount() {
        let time = this.createBranchTime();
        this.setState({ timeArray: time })
    }

    createBranchTime() {
        let time = [];
        const date = new Date();
        const i = this.state.shopData.officeday.findIndex(item => { return item.weekday === date.getDay() });

        if (i === -1) {
            this.setState({
                working: false
            })
            return;
        }

        // change open,close time format ISO 8601 -> 'hh.mm'
        let open = this.state.shopData.officeday[i].open_time.match(/[0-9]*:[0-9]*:[0-9]*/)[0].split(':');
        let close = this.state.shopData.officeday[i].close_time.match(/[0-9]*:[0-9]*:[0-9]*/)[0].split(':')

        open = parseFloat(open[0] + '.' + open[1]);
        close = parseFloat(close[0] + '.' + close[1]);

        if (close < open) close += 24;

        let x = Math.abs(open - close);         // declare x (use for count times)

        if (x % 1 < 0.5 || x % 1 > 0.5) {       // parse hrs to 30min
            x = parseInt(x) * 2;
        }

        let temp = open;                        // init temp
        for (let i = 0; i < x; i++) {
            time.push(temp);
            temp % 1 < 0.2
                ? temp += 0.3       // xx.00
                : temp += 0.7       // xx.30

            if (temp === parseFloat(24)) temp = parseFloat(0)   // check time overflow (24)
        }

        // slice the time that passed
        time = time.slice(time.findIndex(x => x > parseFloat(date.getHours() + '.' + date.getMinutes())) + 1);

        return time;
    }

    changePartySize = (op) => {
        // check that party is empty or max
        if ((this.state.partySize === 0 && op === "-") || (this.state.partySize === this.state.maxParty && op === "+")) return;

        // add or sub
        let temp = op === "+" ? 1 : -1;
        this.setState(() => {
            return {
                partySize: this.state.partySize + temp
            }
        })
    }

    submit = (e) => {
        console.log(this.state);
    }

    handlePartyName = (e) => {
        e.preventDefault();
        this.setState({ partyName: e.target.value })
    }

    handlePartyType = (e) => {
        e.preventDefault();
        this.setState({partyType: e.target.name})
    }


    render() {
        let sm = this.props.sm === true ? '-sm' : '';

        return (
            <BookingContainer>
                {/** Header */}
                <div className={"header" + sm}> Booking </div>

                {/** Select party size */}
                <div className={"party-size-container" + sm}>
                    <a className={"size-button-wrapper" + sm} onClick={() => this.changePartySize('-')}>
                        <img className={"size-button" + sm} src="https://image.flaticon.com/icons/svg/271/271220.svg" />
                    </a>

                    <div className={"party-size" + sm}>{this.state.partySize} คน</div>

                    <a className={"size-button-wrapper" + sm} onClick={() => this.changePartySize('+')}>
                        <img className={"size-button" + sm} src="https://image.flaticon.com/icons/svg/271/271228.svg" />
                    </a>
                </div>

                {/** Select time */}
                <div className="form-inline" id={'form' + sm}>
                    <select className="custom-select my-1 mr-sm-2 form-control-lg"
                        id="inlineFormCustomSelectPref"
                        disabled={!this.state.working}
                        style={{textAlign: "center", textAalignLast: "center"}}>
                        <option>Time</option>
                        {
                            this.state.timeArray.map((num, i) => {
                                return (
                                    <option value={i} key={i} style={{textAlign: "center"}}>{
                                        (((Math.round(num * 100) / 100)).toFixed(2)).replace('.',' : ')
                                    }</option>
                                )
                            })
                        }
                    </select>
                </div>

                {/** Privacy radio check */}
                <FormColumn>
                    <div className="form-check" id="check">
                        <label className="form-check-label" htmlFor="exampleRadios2">Private</label>
                        <input className="form-check-input"
                            type="radio"
                            name="private"
                            value="option1"
                            onChange={this.handlePartyType} />
                    </div>

                    <div className="form-check" id="check">
                        <label className="form-check-label" htmlFor="exampleRadios2" >Public</label>
                        <input className="form-check-input"
                            type="radio"
                            name="public"
                            value="option2"
                            onChange={this.handlePartyType} />
                    </div>
                </FormColumn>

                {/** Party name*/}
                <input className={"party-name" + sm} type="text" placeholder="Party name" value={this.state.partyName || ""} onChange={this.handlePartyName} />

                {/** Drink button*/}
                <div className={`drink-bt + ${sm} + ${this.props.disabledBt === true ? " disabled-bt" : ""}`} onClick={this.submit} >Drink!</div>
            </BookingContainer>
        )
    }
}


const FormColumn = styled.div`
                    width: 100px;
                    display: flex;
                    flex-direction: column;

                    #check {
                        display: flex;
                        justify-content: left;
                    }
                    `

const BookingContainer = styled.form`
                display:flex;
                flex-wrap: wrap;
                flex-direction: row;

                color:white;
                width: 100%;
                padding: 15px;
                padding-left: 20px;
                padding-bottom: 25px;
                border-bottom: solid #7a7a7a 1px;

                /*********************** Normal ***********************/
                .header {
                    flex: 1 100%;
                    font-size: 20px;
                }

                .party-size-container {
                    display: flex;
                    align-items: center;
                    
                    margin-right: 30px;
                }

                .party-size {
                    display: flex;
                    width: 70px;
                    height: 35px;
                    margin-left: 5px;
                    margin-right: 5px;
                    
                    text-align: center;
                    flex-direction: column;
                    justify-content: center;

                    color: black;
                    border-radius: 5px 5px 5px 5px;
                    background-color: white;
                }

                .size-button-wrapper {
                    width: 30px;
                    height: 30px;

                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;

                    border-radius: 100%;
                    background-color: white;

                }

                .size-button {
                    width: 20px;
                    height: 20px;
                    display: flex;
                    cursor: pointer;
                }

                .party-name {
                    width: 370px;
                    height: 30px;
                    margin-top: 5px;
                    padding-left: 10px;

                    display: flex;

                    border: 1px #7a7a7a;
                    background-color: white;   
                }

                .party-name::placeholder {  
                    color: gray;
                }

                .drink-bt {
                    width: 60px;
                    height: 30px;
                    margin-top: 5px;
                    margin-left: 25px;
                    
                    display:flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;

                    color: white;
                    border-radius: 5px;
                    border: gold solid 1px;
                    background-color: black;
                }

                .drink-bt:hover {
                    cursor: pointer;
                    box-shadow: 
                        0 0 5px wheat,
                        0 0 10px wheat,
                        0 0 20px wheat,
                        0 0 21px wheat;
                }
                
                .form-inline {
                    margin-right: 30px;
                }

                .disabled-bt {   
                    pointer-events: none;
                    opacity: 0.4;
                }
                
                /********************* small ***********************/
                .header-sm {
                    flex: 1 100%;
                    font-size: 18px;
                    text-align: left;
                }

                .party-size-container-sm {
                    display: flex;
                    align-items: center;
                }

                .party-size-sm {
                    display: flex;
                    width: 60px;
                    height: 35px;
                    margin-left: 5px;
                    margin-right: 5px;
                    
                    text-align: center;
                    flex-direction: column;
                    justify-content: center;

                    color: black;
                    border-radius: 5px 5px 5px 5px;
                    background-color: white;
                }

                .size-button-wrapper-sm {
                    width: 15px;
                    height: 15px;

                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;

                    border-radius: 100%;
                    background-color: white;

                }

                .size-button-sm {
                    width: 10px;
                    height: 10px;
                    display: flex;
                    cursor: pointer;
                }

                .party-name-sm {
                    width: 200px;
                    height: 30px;
                    margin-top: 5px;
                    padding-left: 10px;

                    display: flex;

                    border: 1px #7a7a7a;
                    background-color: white;   
                }

                .party-name-sm::placeholder {
                    color: gray;
                }

                .drink-bt-sm {
                    width: 60px;
                    height: 30px;
                    margin-top: 5px;
                    margin-left: 25px;
                    
                    display:flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;

                    color: white;
                    border-radius: 5px;
                    border: gold solid 1px;
                    background-color: black;
                }

                .drink-bt-sm:hover {
                    cursor: pointer;
                    box-shadow: 
                        0 0 5px wheat,
                        0 0 10px wheat,
                        0 0 20px wheat,
                        0 0 21px wheat;
                }
                
                #form-sm {
                    margin-left: 15px;
                    margin-right: 15px;
                }
            `